/**affiche les informations d'une machine */
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import swal from 'sweetalert';
import classnames from 'classnames';

import {
    Row,
    Col,
    Progress,
    Card,
    CardBody,
    CardHeader,
    CardFooter,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    NavItem,
    NavLink, } 
from 'reactstrap';
import ContentWrapper from '../Layout/ContentWrapper';
import {url_machines} from '../../../urls'
import HeadingMenu from '../HeadingMenu';
import InfoMachine from './InfoMachine';
import ActiviteMachine from '../Activites/ActiviteMachine';
import StatMachine from '../Statistiques/StatMachine';

class ShowMachine extends Component {
  constructor(props) {
    super(props);
    this.state = {
      machine: {},
      modele: {},
      key: '',
      path: [
        { 
            label: 'Home',
            value: '/dashboard_machine',
        },
        { 
            label: 'Machine',
            value: '/list_machine',
        },
        { 
            label: 'Visualiser',
            value: '',
        }
     ],

     activeStep:'1',
     progressValue: [100, 0, 0],
    };
  }

  componentDidMount() {
    const id = this.props.match.params.id
    console.log(`${url_machines}:${id}`);
    
    // with cloud functions
    axios.get(`${url_machines}${id}`)
    .then((res) => {
        const machine = res.data.data.machine
        const modele = machine.modele
        console.log(res)
        this.setState({
            machine: machine,
            modele: modele,
            key: machine.key,
            isLoading: false
        });
        //heading nav
        this.state.path.push({
            label: this.state.machine.nom,
            value:''            
        })
    })
    .catch((error) => {
        console.log("No such machine!", error);
        swal("Erreur", `Cher GreenManager, veillez selectionner une GreenBox pour obtenir ses informations ${error}`, "warning").then((value) => {
            this.props.history.push("/dashboard_machine/")
        })
    })
  }

  delete(id){
    swal({
        title: 'Etes-vous sre?',
        text: 'La suppression de cette machine est une opration irrversible!',
        icon: 'warning',
        buttons: true,
        dangerMode: true,
      })
      .then((willDelete) => {
        if (willDelete) {
            axios.delete(`${url_machines}${id}`)
            .then((res) => {
                console.log("Document successfully deleted!");
                swal("Supprime!", "Cette GreenBox a bien t supprime", "success").then((value) => {
                    this.props.history.push("/list_machine/")
                })
            })
            .catch((error) => {
                console.error("Error removing document: ", error);
                swal("Desole!", "Cette GreenBox n'a pu tre supprime, veuillez vrifier vos paramtres de connexion", "success")
            });
        }
      });

  }

    toggleStep = activeStep => () => {
        const progessValue1 = [100, 0, 0];
        const progessValue2 = [0, 100, 0];
        const progessValue3 = [0, 0, 100];
        
        if (this.state.activeStep !== activeStep) {
            this.setState({
                activeStep : activeStep
            });
        }
        switch (activeStep) {
            case '1':
                this.setState({
                    progressValue: progessValue1
                });
                break;
            case '2':
                this.setState({
                    progressValue: progessValue2
                });
                break;
            case '3':
                this.setState({
                    progressValue: progessValue3
                });
                break;
            default:
                console.log('default')
        }
    }

  
  render() {
    const level1 = this.state.progressValue[0]
    const level2 = this.state.progressValue[1]
    const level3 = this.state.progressValue[2]
    const colorTabHeader = "success"
    console.log(this.state.machine)

    return ( 
        <ContentWrapper>
            <HeadingMenu path={this.state.path}/>
            <div className="d-flex ml-auto col mb-4">
                {/*updated date */}
                <div className="mr-auto p-0">
                    <small className="mb-0 mt-1 text-dark">Mis  jour le : 04/05/21 14:24:18</small>
                </div>
                <div className="col-xl-2 ml-auto">
                <Link to={`/edit_machine/${this.state.key}`} title="Editer la GreenBox" className="ml-auto mr-3">
                    <Button color="success">
                        Editer
                    </Button>
                </Link>                                    
                <Button className="ml-auto mr-0" color="danger" onClick={this.delete.bind(this, this.state.key)}>
                    Supprimer
                </Button>
                </div>

            </div>
            <Card className="card-default my-1">
                <CardHeader className="pb-0">
                    <Row>
                        <div className="col pr-0" width="20%"> <Progress color={colorTabHeader} value={level1}/> </div>
                        <div className="col p-0"> <Progress color={colorTabHeader} value={level2}/> </div>
                        <div className="col pl-0"> <Progress color={colorTabHeader} value={level3}/> </div>
                    </Row>
                </CardHeader>
                <CardBody className="pt-0 pb-0 pl-14 pr-14">         
                    <Nav tabs justified >
                        <NavItem>
                            <NavLink
                            tag="div"
                            className={classnames( {active: this.state.activeStep === '1'} )}
                            onClick={this.toggleStep('1')}
                            >
                                <h4 className="text-left my-2">Information</h4>
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink
                            tag="div"
                            className={classnames( {active: this.state.activeStep === '2'} )}
                            onClick={this.toggleStep('2')}
                            >
                                <h4 className="text-left my-2">Activite</h4>
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink
                            tag="div"
                            className={classnames( {active: this.state.activeStep === '3'} )}
                            onClick={this.toggleStep('3')}
                            >
                                <h4 className="text-left my-2">Statistiques</h4>
                            </NavLink>
                        </NavItem>
                    </Nav>
                </CardBody>    
                <TabContent activeTab={this.state.activeStep}  className="pt-0">
                    {/*information */}
                    <TabPane id="tabPane1" tabId="1">
                        <InfoMachine machine={this.state.machine}/>
                        <div className="d-flex">
                            <Button
                             className="ml-auto"
                             color="primary"
                             onClick={this.toggleStep('2')}
                            >
                                Suivant
                            </Button>
                        </div>                    
                    </TabPane>
                    {/*activite */}                        
                    <TabPane id="tabPane2" tabId="2">
                        <ActiviteMachine machine={this.state.machine}/>
                        <div className="d-flex">
                            <Button outline color="primary" onClick={this.toggleStep('1')}>
                                Precedent
                            </Button>
                            <Button
                             className="ml-auto"
                             color="success"
                             onClick={this.toggleStep('3')}
                            >
                                Suivant
                            </Button>
                        </div>
                    </TabPane>
                    {/*statistiques */}
                    <TabPane id="tabPane3" tabId="3">      
                        <StatMachine machine={this.state.machine}/>
                        <div className="d-flex">
                            <Button outline color="primary" onClick={this.toggleStep('3')}>
                                Precedent
                            </Button>
                            <Button 
                             className="ml-auto"
                             color="success"
                            >         
                                Terminer
                            </Button>
                        </div>
                    </TabPane>
                </TabContent>                                            
            </Card>
            <Row>

            </Row>
        </ContentWrapper>
    );
 }
}


export default ShowMachine;