import React, { Component } from 'react';
import {
    Row,
    Col,
    Progress,
    Card,
    CardBody,
    CardHeader,
    CardFooter,
    FormGroup,
    Input,
    TabContent,
    TabPane,
    Button,
    Nav,
    NavItem,
    NavLink, } 
from 'reactstrap';


/**Affiche les informations gnrales d'une machine */
class InfoMachine extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        console.log(this.props.machine)
        const machine = this.props.machine
        const { nom, zone, localite, level, created, status, modele } = machine;
        const {pays, rue, ville, quartier} = new Object(localite);
        const {libelle, description, dimension, capacite} = new Object(modele)
        const {longueur, largeur, hauteur} = new Object(dimension);

        return (
            <div className="">
                {/* Info machine */}
                <div className="card b">
                        {/* Nom machine */}
                        <div className="card-header">
                            <div className="float-right mt-2">
                                {status? (
                                    <div className="badge badge-info">disponible</div>
                                ):(
                                    <div className="badge badge-danger">indisponible</div>
                                )}
                            </div>
                            <h4 className="my-2">
                                <span> {nom} </span>
                            </h4>
                        </div>
                        {/* Emplacement */}
                        <div className="card-body bb bt">
                            <h4 className="bg-gray-dark pl-2"> <em className="icon-tag"/> Emplacement</h4>
                            <div className="float-left mr-5">
                                <address>
                                    <strong>{rue}, {quartier}  </strong>
                                    <br/> {ville}
                                    <br/> {pays}
                                </address>
                            </div>
                            <div className="float-left pl-5 mr-5">
                                <img src="img/box/trash_box.jpg" alt='poubelle'></img>
                            </div>
                                {/* Options */}
                            <div className="float-left pl-5">
                                   <Card/>
                                   <div className="media">
                                      <div className="align-self-start mr-2">
                                         <span className="fa-stack">
                                            <em className="fa fa-circle fa-stack-2x text-purple"></em>
                                            <em className="fas fa-map-marker-alt fa-stack-1x fa-inverse text-white"></em>
                                         </span>
                                      </div>
                                      <div className="media-body text-truncate">
                                         <p className="mb-1"><a className="text-purple m-0" href="">Afficher la machine sur la carte</a>
                                         </p>
                                      </div>
                                   </div>
                                   <Card/>
                                   <Card/>
                                   <div className="media">
                                      <div className="align-self-start mr-2">
                                         <span className="fa-stack">
                                            <em className="fa fa-circle fa-stack-2x text-info"></em>
                                            <em className="fas fa-paper-plane fa-stack-1x fa-inverse text-white"></em>
                                         </span>
                                      </div>
                                      <div className="media-body text-truncate">
                                            <p className="mb-1"><a className="text-info m-0" href="">Obtenir l'itinraire</a></p>
                                      </div>
                                   </div>
                                   <Card/>
                            </div>
                        </div>
                        {/*Modele */}
                        <div className="card-body">
                            <h4 className="bg-gray-dark pl-2"> <em className="icon-tag"/> Modle </h4>
                            <Row>
                                <Col xl="8">
                                    <form className="form-horizontal">
                                {/*libelle */}
                                <div className="form-group row">
                                    <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="nom">Libell</label>
                                    <div className="col-xl-10 col-md-9 col-8">
                                        <input className="form-control" id="nom" type="text" placeholder="" value={libelle}/>
                                    </div>
                                </div>
                                {/*description */}
                                <div className="form-group row">
                                    <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="description"> Description </label>
                                    <div className="col-xl-10 col-md-9 col-8">
                                        <input className="form-control" id="description" type="text" value={description}/>
                                    </div>
                                </div>
                                {/*capacite */}
                                <div className="form-group row">
                                    <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="capacite">Capacite</label>
                                    <div className="col-xl-10 col-md-9 col-8">
                                        <input className="form-control" id="capacite" type="text" value={capacite}/>
                                    </div>
                                </div>
                                {/*dimension */}
                                <div className="form-group row">
                                    <label className="text-bold col-xl-2 col-md-3 col-4 col-form-label text-right" htmlFor="dimension">Dimension</label>
                                    <div className="flex-direction-column">
                                    <div className="form-group row">
                                        <label className="col-xl-4 col-md-3 col-4 col-form-label text-right" htmlFor="longueur">Longueur</label>
                                        <div className="col-xl-8 col-md-9 col-8">
                                            <input className="form-control" id="longueur" type="text" value={longueur}/>
                                        </div>
                                    </div>
                                    <div className="form-group row">
                                        <label className="col-xl-4 col-md-3 col-4 col-form-label text-right" htmlFor="largeur">Largeur</label>
                                        <div className="col-xl-8 col-md-9 col-8">
                                            <input className="form-control" id="largeur" type="text" value={largeur}/>
                                        </div>
                                    </div>
                                    <div className="form-group row">
                                        <label className="col-xl-4 col-md-3 col-4 col-form-label text-right" htmlFor="hauteur">Hauteur</label>
                                        <div className="col-xl-8 col-md-9 col-8">
                                            <input className="form-control" id="hauteur" type="text" value={hauteur}/>
                                        </div>
                                    </div>                                                                        
                                    </div>
                                </div>
                            </form>
                                </Col>
                                <Col xl="4">
                                    <Card className="bg-gray-light">
                                        <CardHeader/>
                                        <CardBody>
                                            PHOTO ICI
                                        </CardBody>
                                        <CardFooter/>
                                    </Card>
                                </Col>
                            </Row>
                            <p className="text-right">
                                <button className="btn btn-secondary" type="button" color="info">Vu 3D</button>
                            </p>
                        </div>
                        {/*another part */}
                </div>
                {/* End Main card */}
            </div>
        )
    }
}

export default InfoMachine;